import React from "react";
import {
  Header,
  Group,
  Banner,
  Button
} from "@vkontakte/vkui";

const About = () => {
  
    return (
        <Group header={<Header multiline mode='secondary'> </Header>}>
            <Banner
                mode='image'
                size='m'
                header={<span>github</span>}
                subheader=' '
                background={
                <div
                    style={{
                    backgroundColor: '#000',
                    backgroundImage:
                        'url(https://docplayer.es/docs-images/110/193906487/images/9-3.jpg)',
                    backgroundPosition: 'right 5% bottom 50%',
                    backgroundSize: 100,
                    backgroundRepeat: 'no-repeat',
                    }}
                />
                }
                actions={
                <Button 
                    mode='overlay_primary'
                    size='m' 
                    href='https://github.com/lyaplyap/psychological_tests_miniapp' 
                    target='_blank'
                >
                    
                </Button>
                }
            />
            <Banner
                mode='image'
                size='m'
                header={<span>dscs.pro</span>}
                subheader='     '
                background={
                <div
                    style={{
                    backgroundColor: '#003368',
                    backgroundImage:
                        'url(https://dscs.pro/wp-content/uploads/2022/01/dscs.pro_-1.png)',
                    backgroundPosition: 'left bottom',
                    backgroundSize: 1000,
                    backgroundRepeat: 'no-repeat',
                    }}
                />
                }
                actions={
                <Button 
                    mode='overlay_primary'
                    size='m' 
                    href='https://dscs.pro' 
                    target='_blank'
                >
                    
                </Button>
                }
            />
        </Group>
    );
  };

export default About;