import React, { useState, useEffect } from "react";
import {
  ConfigProvider, 
  AdaptivityProvider,
  usePlatform,
  AppRoot,
} from "@vkontakte/vkui";
import bridge from '@vkontakte/vk-bridge';
import "@vkontakte/vkui/dist/vkui.css";

import MainMenu from "./panels/MainMenu";

const App = () => {
  const [user, setUser] = useState({});

  const [settings, setSettings] = useState({
    platform: usePlatform(),
    scheme: 'bright_light'
  });

  useEffect(() => {
    bridge
      .send("VKWebAppGetUserInfo")
      .then(data => {
        delete data.id;
        setUser(user => ({ ...user, ...data }));
      })
  }, []);

  return (
    <ConfigProvider isWebView={true} platform={settings.platform} scheme={settings.scheme}
    >
      <AdaptivityProvider>
      <AppRoot>
        <MainMenu
          user={user}
          setUser={setUser}
          settings={settings}
          setSettings={setSettings}
        />
      </AppRoot>
      </AdaptivityProvider>
    </ConfigProvider>
  );
}

export default App;